#navigation{
    background-color: $bg-black;
    border: 3px solid $border-yellow;
    margin-top: 20px;
    text-shadow: 0.1em 0.1em $bg-black;
    height:210px;
    
    @include atMedium{
        height: 60px;
    }
     
}

#nav{
    list-style: none;
        
    ul{
        margin:0;
        padding:0;
        width: auto;
        
        @include atMedium{
            display:inline-block;
        }
       
        
    }
    
    li{
        font-size: 24px;
        color:#817b7b;
        float:left;
        position: relative;
        height: 50px;
        width: 350px;
        
        @include atMedium{
            width:150px;
        
        }
        

    
    }
    
    a:link, nav a:active nav a:visited{
        display: block;
        color: $white;
        text-decoration:none;
    
    }
     
    a:hover{
        color: lightblue;
    
    }

}
